html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 5px;
  padding: 3px;
}

body {
  background-color: lightseagreen;
  margin: 0px;
  padding: 0px;
}
ul {
  list-style-image: url(./components/ContactList/image/social-group.svg);
  list-style-position: inside;
}

button {
  font-weight: 600;
  padding: 0px 1px;
  border-style: dotted;
}

button:hover,
button:focus {
  cursor: pointer;
  background-color: tomato;
  outline: none;
}

form {
  width: 280px;
  border-style: hidden;
  box-shadow: 0 0 5px 2px;
}

li {
  border-style: hidden;
  width: 260px;
  margin: 5px;
  background-color: white;
  /* box-shadow: 0.4em 0.4em 5px rgba(122, 122, 122, 0.5); */
  box-shadow: 0 0 5px 2px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

h1,
h2,
h3 {
  color: whitesmoke;
  text-shadow: 2px 2px 2px black, 0 0 0 black;
}
